import Customs from "./customService.js";
import router from "../../router";
import swal from "sweetalert";

export default {
    name: 'ProjectDetail',
    components: {

    },
    data: () => ({
        id: "",
        userId: "",
        project: {
            name: "",
            description: ""
        },
        projectLoaded: false
    }),

    methods: {
        navigate() {
            router.go(-1);
        },
        deleteProject(projectID) {
            this.$http
            .delete("/project/delete/" + projectID).then(response => {
                swal("Success", response.data.message, "success");
                this.navigate();
                return (response);
            });
        },
        getProject() {
            this.$http
            .post("/project/byId/", {
                id: this.id
            })
            .then(response => {
                if (response.data.project != null) {
                    this.project = response.data.project;
                    this.projectLoaded = true;
                }
                //show "NO EXIST ANYMORE :O"
                
            });
            return null;
        },
    },

    created() {
        this.id = this.$route.params.id;
        let user = Customs.getUserDetails();
        this.userId = user._id;
        this.getProject();
    },
};